
<template class="bg-gradient-info">



    <div class="container">

        <div class="card o-hidden border-0 shadow-lg my-5">
            <div class="card-body p-0">

                <div class="row">
                    <div class="col-lg-5 d-none d-lg-block "></div>
                    <div class="col-lg-7">
                        <div class="p-5">
                            <div class="text-center">
                                <h1 class="h4 text-gray-900 mb-4">Crea tu Cuenta!</h1>
                            </div>
                            <form class="user" >
                                <div class="form-group row">
                                    <div class="col-sm-6 mb-3 mb-sm-0">
                                        <input type="text" class="form-control form-control-user" id="exampleFirstName"
                                            placeholder="Apellido" v-model="form.Apellidos">
                                    </div>
                                    <div class="col-sm-6">
                                        <input type="text" class="form-control form-control-user" id="exampleLastName"
                                            placeholder="Nombre" v-model="form.Nombres">
                                    </div>
                                    
                                </div>
                                <div class="form-group row">
                                    <div class="col-sm-6 mb-3 mb-sm-0">
                                        <input type="text" class="form-control form-control-user" id="exampleFirstName"
                                            placeholder="Identificacion" v-model="form.Identificacion">
                                    </div>
                                    <div class="col-sm-6">
                                        <input type="text" class="form-control form-control-user" id="exampleLastName"
                                            placeholder="Telefono" v-model="form.Telefono">
                                    </div>
                                    
                                </div>
                                <div class="form-group">
                                    <input type="email" class="form-control form-control-user" id="exampleInputEmail"
                                        placeholder="Email" v-model="form.Email">
                                </div>

                                <!-- <div class="form-group row">
                                    <div class="col-sm-6 mb-3 mb-sm-0">
                                        <input type="password" class="form-control form-control-user"
                                            id="exampleInputPassword" placeholder="Contraseña">
                                    </div>
                                    <div class="col-sm-6">
                                        <input type="password" class="form-control form-control-user"
                                            id="exampleRepeatPassword" placeholder="Confirma Contraseña">
                                    </div>
                                </div> -->

                                <button class="btn btn-info btn-user btn-block" v-on:click="Registrar()">
                                    Registrarse
                                </button>

                    
                            </form>
                            <hr>
                            <div class="text-center">
                                <a class="small" href="forgot-password.html">Olvidaste Contraseña?</a>
                            </div>
                            <div class="text-center">
                                <router-link class="small" to="/Home">Ya tienes Cuenta? Iniciar Sesion!</router-link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>


</template>

<script>
import axios from 'axios'


export default {

    name:"Registrer",

    data(){
        return{
            form:{
                " Nombres":"",
                "Apellidos":"",
                "Identificacion":"",
                "Telefono":"",
                "Email":""
            }
        }

    },

    methods:{
        Registrar(){
            axios
            .post("https://api.solodata.es/pacientes", this.form)
            .then(datos =>{
                console.log(datos)
                this.$router.push('/home')
            })
        }
    }

   
}


</script>

<style scoped src="@/css/estylo.css"> 





</style>